import { useState } from "react";
import "./works.scss";

export default function Works() {
  const [currentSlide, setCurrentSlide] = useState(0);
  const data = [
    {
      id: "1",
      icon: "./assets/mobile.png",
      title: "EduHub",
      desc: "A site for student of BCA student which has subject wise all notes, topic wise videos and question papers. ",
      img: "./assets/edu2.png",
      link: "https://educationhhub.000webhostapp.com/index.html",
    },
    {
      id: "2",
      icon: "./assets/globe.png",
      title: "Graph Simulation",
      desc: "A site to simulate various graph algorithms visually like DFS, BFS, Dijkstra, MST .IT also have covid simulation.",
      img: "./assets/gta2.png",
      link: "https://gtas-21c0f.web.app/",
    },
    {
      id: "3",
      icon: "./assets/writing.png",
      title: "Network Simulation",
      desc: "Simulated and analyzed packet delay ratio, throughput, End-to-End delay of various network topologies with the help of NAM and Xgraph.",
      img: "./assets/ns22.png",
      link: "https://github.com/samarth207/ns-2-basic-simulations-.git",
    },
  ];

  const handleClick = (way) => {
    way === "left"
      ? setCurrentSlide(currentSlide > 0 ? currentSlide - 1 : 2)
      : setCurrentSlide(currentSlide < data.length - 1 ? currentSlide + 1 : 0);
  };

  return (
    <div className="works" id="works">
      <h1
        style={{
          position: "absolute",
          top: "50px",
          color: "white",
          fontFamily: "arial",
          fontWeight: "600",
          fontSize: "45px",
        }}
      >
        Featured
      </h1>
      <div
        className="slider"
        style={{ transform: `translateX(-${currentSlide * 100}vw)` }}
      >
        {data.map((d) => (
          <div className="container">
            <div className="item">
              <div className="left">
                <div className="leftContainer">
                  <div className="imgContainer">
                    <img src={d.icon} alt="" />
                  </div>
                  <h2>{d.title}</h2>
                  <p>{d.desc}</p>
                  <a href={d.link} target="_blank">
                    <span>Project Link</span>
                  </a>
                </div>
              </div>
              <div className="right">
                <img src={d.img} alt="" />
              </div>
            </div>
          </div>
        ))}
      </div>
      <img
        src="assets/arrow.png"
        className="arrow left"
        alt=""
        onClick={() => handleClick("left")}
      />
      <img
        src="assets/arrow.png"
        className="arrow right"
        alt=""
        onClick={() => handleClick()}
      />
    </div>
  );
}
